<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process211"></a>

<p><br></p>
<p><span class="rvts84">章节简介</span></p>
<p><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1270.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts21">锯木厂是孤狼的指挥部，里面有很多可以做的</span><span class="rvts20"></span><span class="rvts21">复仇</span><span class="rvts20"></span><span class="rvts21">任务。血月岛则是</span><span class="rvts20"></span><span class="rvts21">死神海岸</span><span class="rvts20"></span><span class="rvts21">地图的最终结点，通过杀掉提倡者并和先祖之树交谈，便可以进入下一张地图</span><span class="rvts20"></span><span class="rvts21">无名岛</span><span class="rvts20"></span><span class="rvts21">。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



